import { Component, OnInit, OnDestroy } from '@angular/core';
import { AuthService } from '../../services/auth.service';
import { Response } from '@angular/http';
import { FlashMessagesService } from 'angular2-flash-messages';
import { SocketService } from '../../services/socket.service';

@Component({
  selector: 'app-scoreboard',
  templateUrl: './scoreboard.component.html',
  styleUrls: ['./scoreboard.component.css'],
  providers: [SocketService]
})
export class ScoreboardComponent implements OnInit, OnDestroy {
  scores: any;
  connection: any;

  constructor(private authService: AuthService,
     private flashMessages: FlashMessagesService,
     private socketService: SocketService
    ) { }

  ngOnInit() {
    this.updateScore();
    this.connection = this.socketService.getUpdate().subscribe((data: Response) => {
      this.updateScore();
    });
    console.log(this.connection);
  }

  ngOnDestroy() {
    this.connection.unsubscribe();
  }



  updateScore() {
    this.authService.getScore().subscribe((scores: Response) => {
      this.scores = scores.json().scores;
      this.flashMessages.show('Score Updated', {cssClass: 'alert-success', timeout: 3000});
    }, err => {
      console.log(err);
      return false;
    });
  }

}



