import { Component, OnInit } from '@angular/core';
import { Response } from '@angular/http';
import { AuthService } from '../../services/auth.service';
import { Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';
import { ValidateService } from '../../services/validate.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  username: string;
  password: string;

  constructor(private authService: AuthService,
     private router: Router,
     private flashMessages: FlashMessagesService,
     private validateService: ValidateService
    ) { }

  ngOnInit() {
  }

  onLoginSubmit() {
    const user = {
      username: this.username,
      password: this.password
    }

    if(this.validateService.validateLogin(user)) {
      this.authService.authenticateUser(user).subscribe((res: Response) => {
        const data = res.json();
        if(data.success) {
          this.authService.storeUserData(data.token, data.user);
          this.flashMessages.show('You are now logged in', {cssClass: 'alert-success', timeout: 3000});
          this.router.navigate(['/scoreboard']);
        } else {
          this.flashMessages.show(data.msg, {cssClass: 'alert-danger', timeout: 3000});
          this.router.navigate(['/login']);
        }
      });
    } else {
      this.flashMessages.show('Fields cannot be empty', {cssClass: 'alert-danger', timeout: 3000});
      this.router.navigate(['/login']);
    }
    
  }

}


