import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { tokenNotExpired } from 'angular2-jwt';

@Injectable()
export class AuthService {
  authToken: any;
  user: any;

  constructor(private http: Http) { }

  registerUser(user) {
    let headers = new Headers();
    headers.append('Content-type', 'application/json');
    return this.http.post('http://localhost:8000/users/register', user, {headers: headers});
  }

  authenticateUser(user) {
    let headers = new Headers();
    headers.append('Content-type', 'application/json');
    return this.http.post('http://localhost:8000/users/authenticate', user, {headers: headers});
  }

  getProfile() {
    let headers = new Headers();
    headers.append('Content-type', 'application/json');
    this.loadToken();
    headers.append('Authorization', this.authToken);
    return this.http.get('http://localhost:8000/users/profile', {headers: headers});
  }

  storeUserData(token, user) {
    localStorage.setItem('id_token', token);
    localStorage.setItem('user', JSON.stringify(user));
    this.authToken = token;
    this.user = user;
  }

  loadToken() {
    const token = localStorage.getItem('id_token');
    this.authToken = token;
  }

  loggedIn() {
    return tokenNotExpired('id_token');
  }

  isAdmin() {
    let user = JSON.parse(localStorage.getItem('user'));
    return user.isAdmin;
  }

  logout() {
    this.authToken = null;
    this.user = null;
    localStorage.clear();
  }

  getScore() {
    let headers = new Headers();
    headers.append('Content-type', 'application/json');
    return this.http.get('http://localhost:8000/scores/all', {headers: headers});
  }

  updateScore(score) {
    let headers = new Headers();
    headers.append('Content-type', 'application/json');
    return this.http.post('http://localhost:8000/scores/add', score, {headers: headers});
  }

}


